import React from 'react';

import Flex from 'components/Flex';
import Box from 'components/Box';
import Text from 'components/Text';
import Circle from 'components/Circle';

import { mobileOrDesktop } from 'components/ThemeProvider/theme';

const Title = ({ title, subtitle, children, ...props }) => {
  return (
    <Box color="titleBlue" {...props}>
      <Flex justifyContent="center">
        <Flex flex="1" alignItems="center" pl="1em" justifyContent="flex-end">
          <Box width={mobileOrDesktop('3em', '4em')} py="1.5px" bg="lightPurple" />
          <Circle width="0.75em" bg="lightPurple" />
        </Flex>
        <Box px="1em">
          <Text.h2 fontSize={mobileOrDesktop('1.5em', '1.75em')}>{title || children}</Text.h2>
        </Box>
        <Flex flex="1" alignItems="center" pr="1em">
          <Circle width="0.75em" bg="lightPurple" />
          <Box width={mobileOrDesktop('3em', '4em')} py="1.5px" bg="lightPurple" />
        </Flex>
      </Flex>
      {subtitle &&
        <Flex justifyContent="center">
          <Text mt="1em" fontSize="1.25em" textAlign="left" whiteSpace={mobileOrDesktop('pre-wrap', 'unset')} color="titleBlue">{subtitle}</Text>
        </Flex>
      }
    </Box>
  );
};

export default Title;
